'use client'

import { useState } from 'react'
import { useData, Material } from '@/lib/data-context'
import { AuthLayout } from '@/components/auth-layout'
import { MaterialForm } from '@/components/materials/material-form'
import { MaterialList } from '@/components/materials/material-list'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { useConfirmDialog } from '@/hooks/use-confirm-dialog'
import { ConfirmDialog } from '@/components/confirm-dialog'
import { InfoDialog } from '@/components/info-dialog'
import { useToast } from '@/hooks/use-toast'
import { PageHeader } from '@/components/page-header'

export default function MaterialsPage() {
  const { materials, addMaterial, updateMaterial, deleteMaterial, suppliers } = useData()
  const [isFormVisible, setIsFormVisible] = useState(false)
  const [editingMaterial, setEditingMaterial] = useState<Material | null>(null)
  const [infoDialog, setInfoDialog] = useState({ isOpen: false, title: '', description: '', variant: 'info' as 'info' | 'warning' | 'success' })
  const { toast } = useToast()
  const confirmDialog = useConfirmDialog()

  const lowStockCount = materials.filter(m => m.quantity <= m.minQuantity).length
  const totalValue = materials.reduce((acc, m) => acc + (m.quantity * m.unitPrice), 0)

  const handleAddNew = () => {
    if (suppliers.length === 0) {
      setInfoDialog({
        isOpen: true,
        title: 'Fornecedor Necessário',
        description: 'Você precisa cadastrar pelo menos um fornecedor antes de criar um material. Vá para a página de Fornecedores para cadastrar.',
        variant: 'warning',
      })
      return
    }
    setEditingMaterial(null)
    setIsFormVisible(true)
  }

  const handleEdit = (material: Material) => {
    setEditingMaterial(material)
    setIsFormVisible(true)
  }

  const handleSubmit = (data: Omit<Material, 'id' | 'lastUpdate'>) => {
    if (editingMaterial) {
      updateMaterial(editingMaterial.id, data)
      toast({
        title: 'Material atualizado',
        description: 'Os dados do material foram atualizados com sucesso.',
      })
    } else {
      addMaterial(data)
      toast({
        title: 'Material cadastrado',
        description: 'O material foi cadastrado com sucesso.',
      })
    }
    setIsFormVisible(false)
    setEditingMaterial(null)
  }

  const handleCancel = () => {
    setIsFormVisible(false)
    setEditingMaterial(null)
  }

  const handleDelete = async (id: string) => {
    const material = materials.find(m => m.id === id)
    const confirmed = await confirmDialog.confirm({
      title: 'Excluir Material',
      description: `Tem certeza que deseja excluir o material "${material?.name}"? Esta ação não pode ser desfeita e pode afetar requisições relacionadas.`,
      confirmText: 'Excluir',
      cancelText: 'Cancelar',
      variant: 'destructive',
    })

    if (confirmed) {
      deleteMaterial(id)
      toast({
        title: 'Material excluído',
        description: 'O material foi excluído com sucesso.',
        variant: 'destructive',
      })
    }
  }

  return (
    <AuthLayout>
      <div className="p-6 lg:p-8">
        <PageHeader
          title="Gestão de Materiais"
          description="Cadastre, atualize e controle o estoque de materiais"
        />

        {!isFormVisible && (
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <Card className="p-5 border border-border/50 hover:border-primary/50 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Total de Materiais</div>
              <div className="text-2xl font-semibold text-foreground">{materials.length}</div>
            </Card>
            <Card className="p-5 border border-destructive/20 bg-destructive/5 hover:border-destructive/40 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Itens com Estoque Baixo</div>
              <div className="text-2xl font-semibold text-destructive">{lowStockCount}</div>
            </Card>
            <Card className="p-5 border border-border/50 hover:border-secondary/50 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Valor Total Estoque</div>
              <div className="text-2xl font-semibold text-foreground">R$ {totalValue.toFixed(2)}</div>
            </Card>
          </div>
        )}

        <div className="mb-6">
          <Button onClick={handleAddNew} className="bg-primary hover:bg-primary/90 font-medium">
            Novo Material
          </Button>
        </div>
        <MaterialList
          materials={materials}
          onEdit={handleEdit}
          onDelete={handleDelete}
        />

        <MaterialForm
          material={editingMaterial || undefined}
          isOpen={isFormVisible}
          onSubmit={handleSubmit}
          onCancel={handleCancel}
        />
      </div>

      <ConfirmDialog
        isOpen={confirmDialog.isOpen}
        title={confirmDialog.options.title}
        description={confirmDialog.options.description}
        confirmText={confirmDialog.options.confirmText}
        cancelText={confirmDialog.options.cancelText}
        variant={confirmDialog.options.variant}
        onConfirm={confirmDialog.handleConfirm}
        onCancel={confirmDialog.handleCancel}
      />

      <InfoDialog
        isOpen={infoDialog.isOpen}
        title={infoDialog.title}
        description={infoDialog.description}
        variant={infoDialog.variant}
        onClose={() => setInfoDialog({ ...infoDialog, isOpen: false })}
      />
    </AuthLayout>
  )
}
