'use client'

import { useState, useMemo } from 'react'
import { Material } from '@/lib/data-context'
import { useData } from '@/lib/data-context'
import { Button } from '@/components/ui/button'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import { Card } from '@/components/ui/card'
import { Edit, Trash2, MoreVertical, Search } from 'lucide-react'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'

interface MaterialListProps {
  materials: Material[]
  onEdit: (material: Material) => void
  onDelete: (id: string) => void
}

export function MaterialList({ materials, onEdit, onDelete }: MaterialListProps) {
  const { suppliers } = useData()
  const [searchQuery, setSearchQuery] = useState('')

  const getSupplierName = (supplierId: string) => {
    return suppliers.find(s => s.id === supplierId)?.name || 'Desconhecido'
  }

  const getCategoryLabel = (category: string) => {
    const categories: Record<string, string> = {
      escritorio: 'Material de Escritório',
      escolar: 'Material Escolar',
      limpeza: 'Limpeza e Higiene',
      tecnologia: 'Tecnologia',
      esportes: 'Esportes e Recreação',
      outro: 'Outro'
    }
    return categories[category] || category
  }

  const filteredMaterials = useMemo(() => {
    if (!searchQuery || searchQuery.trim() === '') {
      return materials
    }

    const query = searchQuery.trim().toLowerCase()
    
    return materials.filter(material => {
      const nameMatch = material.name.toLowerCase().includes(query)
      const descriptionMatch = material.description && material.description.toLowerCase().includes(query)
      const supplierName = getSupplierName(material.supplier).toLowerCase()
      const supplierMatch = supplierName.includes(query)
      const categoryLabel = getCategoryLabel(material.category).toLowerCase()
      const categoryMatch = categoryLabel.includes(query)
      const unitMatch = material.unit.toLowerCase().includes(query)
      
      return nameMatch || descriptionMatch || supplierMatch || categoryMatch || unitMatch
    })
  }, [materials, searchQuery, suppliers])

  if (materials.length === 0) {
    return (
      <Card className="p-12 text-center">
        <p className="text-muted-foreground">Nenhum material cadastrado ainda</p>
      </Card>
    )
  }

  return (
    <div className="space-y-4">
      {/* Barra de Pesquisa */}
      <div className="relative">
        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
        <Input
          type="text"
          placeholder="Pesquisar por nome, descrição, categoria, fornecedor ou unidade..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="pl-10"
        />
      </div>

      {filteredMaterials.length === 0 && searchTerm.trim() !== '' ? (
        <Card className="p-12 text-center">
          <p className="text-muted-foreground">Nenhum material encontrado com o termo "{searchTerm}"</p>
        </Card>
      ) : (
        <>
          {filteredMaterials.map(material => {
        const isLow = material.quantity <= material.minQuantity
        return (
          <Card key={material.id} className={`p-4 ${isLow ? 'border-accent bg-accent/5' : ''}`}>
            <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-2">
                  <h3 className="font-semibold text-lg">{material.name}</h3>
                  {isLow && (
                    <span className="px-2 py-1 text-xs bg-accent text-accent-foreground rounded-full">
                      Estoque Baixo
                    </span>
                  )}
                </div>
                <p className="text-sm text-muted-foreground mb-2">{material.description}</p>
                <div className="grid grid-cols-2 md:grid-cols-5 gap-2 text-sm text-muted-foreground">
                  <div><strong>Categoria:</strong> {getCategoryLabel(material.category)}</div>
                  <div><strong>Fornecedor:</strong> {getSupplierName(material.supplier)}</div>
                  <div><strong>Estoque:</strong> {material.quantity} {material.unit}</div>
                  <div><strong>Mínimo:</strong> {material.minQuantity}</div>
                  <div><strong>Valor Unit.:</strong> R$ {material.unitPrice.toFixed(2)}</div>
                </div>
              </div>
              <div className="flex justify-end">
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
                      <MoreVertical className="h-4 w-4" />
                      <span className="sr-only">Abrir menu</span>
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem onClick={() => onEdit(material)}>
                      <Edit className="h-4 w-4 mr-2" />
                      Editar
                    </DropdownMenuItem>
                    <DropdownMenuItem
                      onClick={() => onDelete(material.id)}
                      variant="destructive"
                    >
                      <Trash2 className="h-4 w-4 mr-2" />
                      Excluir
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </div>
            </div>
          </Card>
          )
        })}
        {filteredMaterials.length > 0 && (
          <div className="text-sm text-muted-foreground">
            Mostrando {filteredMaterials.length} de {materials.length} material(is)
          </div>
        )}
        </>
      )}
    </div>
  )
}
